import { Injectable } from '@angular/core';
import { AppService } from './services/app.service';
import { Device } from '@ionic-native/device/ngx';
import { Router } from '@angular/router';



@Injectable({
  providedIn: 'root'
})
export class UserService {


  

  constructor(private _AppService:AppService,private _Device: Device,private _Router:Router) { }


  is_user_logged(is_go_home){
    if(localStorage.getItem('token') != null){
      // console.log(localStorage.getItem('token'))
      if(is_go_home){
        this._AppService.router_navigation('/home')
      }
      return true
    }
  }


is_user_logged_routing(){
  if(localStorage.getItem('token') != null){
    if(this._Router.url == '/login' || this._Router.url == '/register' || this._Router.url == '/intro'){
      this._AppService.router_navigation('/home')
    } 
  } else {
    // this._AppService.router_navigation('/intro')
  }
}

  get_device_id() {
    let device_id = this._Device.uuid
    return device_id

  }
}
