import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders, HttpEventType } from '@angular/common/http';
import { Router } from '@angular/router';
import { NavController } from '@ionic/angular';
import { Platform } from '@ionic/angular'


@Injectable({
  providedIn: 'root'
})
export class AppService {
  // block_origin = 'https://cors-anywhere.herokuapp.com/'


  base_url = 'https://arriyadiyah.com/api/'
  so3ody_url = 'https://matches.arriyadiyah.com/api/'
  // so3ody_url = 'https://arriyadiyah.mediapanarab.com/api/'
  // so3ody_url = 'http://arriyadiyah2.mediapanarab.com/api/'
  
  // so3ody_url = 'https://scoresmigrator.mediapanarab.com/api/soccer/'

  constructor(private _http: HttpClient, private _Router: Router, private _NavController:NavController,private _Platform:Platform) { }

  get_data(url, domain) {
    let _base
    if (domain == 's') {
      _base = this.so3ody_url
    } else {
      _base = this.base_url
    }
    return this._http.get(`${_base}${url}`)
  }
  upload(url, data) {
    return this._http.post(`${this.base_url}${url}`, data)
  }
  post_data(url, data,domain){
    let _base
    if (domain == 's') {
      _base = this.so3ody_url
    } else {
      _base = this.base_url
    }
    return this._http.post(`${_base}${url}`, data)
  }

  user_post_data(url, data,domain,_token) {
    const headers = new HttpHeaders({
      'Content-Type': 'application/json',
      'Authorization': `Bearer ${_token}`
    })
    let _base
    if (domain == 's') {
      _base = this.so3ody_url
    } else {
      _base = this.base_url
    }
    return this._http.post(`${_base}${url}`, data,{ headers: headers })
  }

  user_get_data(url, domain,_token) {
    const headers = new HttpHeaders({
      'Content-Type': 'application/json',
      'Authorization': `Bearer ${_token}`
    })
    let _base
    if (domain == 's') {
      _base = this.so3ody_url
    } else {
      _base = this.base_url
    }
    return this._http.get(`${_base}${url}`,{ headers: headers })
  }

  getPostDataForNotify(postId){
    // const headers = new HttpHeaders({
    //   'Content-Type': 'application/json',
    //   // 'Authorization': `Bearer ${_token}`
    // })
    return this._http.get(`https://arriyadiyah.com/api/article/${postId}`)
  }


  


  tabs(el, box, _class) {
    let _tabs, _boxes
    _tabs = document.querySelectorAll(el)
    _boxes = document.querySelectorAll(box)
    for (let i = 0; i < _tabs.length; i++) {
      // console.log( _tabs[i])
      // console.log(_boxes[i])
      _tabs[i].addEventListener('click', () => {
        document.querySelector(el + '.' + _class).classList.remove(_class)
        document.querySelector(box + '.' + _class).classList.remove(_class)
        _tabs[i].classList.add(_class)
        _boxes[i].classList.add(_class)
      })
    }
  }

  scroll_to_top() {
    document.querySelector('.wrapper').scrollTo(0, 0);
  }

  router_navigation(route_url) {
    this._Router.navigate([`/${route_url}`]);
    // this._NavController.navigateRoot(route_url)
  }

  go_back(){
    this._NavController.pop();
  }


  pull_to_refresh(event) {
    setTimeout(() => {
      // event.target.complete()
      window.location.reload();

    }, 500);
    // 
  }


  get_platform() {
    let _platform
    if (this._Platform.is('android')) {
      _platform = 'android'
    } else if (this._Platform.is('ios')) {
      _platform = 'ios'
    } else {
      _platform = 'other'
    }
    console.log(_platform)
    return _platform
  }


  
  


}
