  async presentToastWithOptions(text,Id) {
    const toast = await this.toastController.create({
      duration:5000 ,
      position: 'top',
      cssClass:"toastbutton",
      buttons: [
          {
          text: `${text}`,
          handler: () => {
            this._Router.navigate([`/singlenotify/${Id}`])
          },
          cssClass:""
        }
      ]
    });
    await toast.present();
  }


